import { ref, computed, watch, getCurrentInstance } from "vue";
import { useStore } from "vuex";
export default {
  name: "qr_content",
  props: {
    msg: String
  },

  setup() {
    let InputValue = ref("");
    let SendingAddress = "";
    const store = useStore();
    let RequestUrl = store.state.RequestUrl; //用计算属性取到vuex中的值

    const getShowTask = computed(() => {
      return store.state.RequestUrl;
    }); //监视vux中请求地址的变化

    watch(getShowTask, (newVal, oldVal) => {
      RequestUrl = newVal;
      console.log("RequestUrl变化了");
    }); //点击生成二维码事件

    function become() {
      if (InputValue.value.includes("www") || InputValue.value.includes("com") || InputValue.value.includes("cn")) {
        if (InputValue.value.includes("http://") || InputValue.value.includes("https://")) {} else {
          toast("输入内容可能是网址，将自动添加http请求头", "top");
          InputValue.value = "http://" + InputValue.value;
        }
      }

      if (InputValue.value) {
        SendingAddress = RequestUrl + InputValue.value; // loadShow.value = true;
        // 触发vuex中的actions，用于异步修改state的信息

        store.dispatch("upSendingAddress", SendingAddress);
      } else {
        toast("还未输入信息哦", "top");
      }
    } //这里的proxy代替this


    const {
      proxy
    } = getCurrentInstance(); //封装轻提示

    const toast = (message, position) => {
      proxy.$toast({
        message: message,
        position: position
      });
    };

    return {
      InputValue,
      become,
      toast
    };
  }

};